import { _decorator, Component, Node, Vec3, Vec2, Touch, EventMouse, instantiate, director, BoxCollider2D, Collider2D, RigidBody, Collider, physics, PhysicsSystem } from 'cc';
import ReaveldCard from './ReaveldCard';
const { ccclass, property } = _decorator;

@ccclass('WinPile')
export class WinPile extends Component {

    start() {
        PhysicsSystem.instance.enable = true;
        let winCollider = this.node.getComponent(Collider);
        winCollider.on("onCollisionEnter", this.onCollisionEnter, this)
    }

    onCollisionEnter(self: Collider, other: Collider) {
        console.log("self", self);
        console.log("other", other);


    }
}

